html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Sarabun", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: #434343;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 2.2rem;
}

.hidden {
  display: none !important;
}

.bnw {
  filter: grayscale(100%); /* Aplicar escala de grises al 100% */
}

.zoomed-out {
  cursor: zoom-out;
}

.zoomed-in {
  cursor: zoom-in;
}

.normal {
  cursor: default;
}

.header {
  padding: 1.5rem 0;
  border-bottom: 0.1rem solid #7d88d5;
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-content h1 {
  color: #7d88d5;
  font-family: "Lobster", cursive;
  font-size: 3.4rem;
  margin: 0;
}
.header-content h1 span {
  color: #6069a9;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-btn {
  background-color: #7d88d5;
  color: #fff;
  padding: 10px 20px;
  border-radius: 0.3rem;
  cursor: pointer;
  border: none;
  transition: background-color 0.3s ease;
}
.dropdown-btn:hover {
  background-color: #6069a9;
}

.dropdown-content {
  display: none;
  position: absolute;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  right: 0;
}

/* Estilos para los elementos desplegables */
.dropdown-content a {
  display: block;
  padding: 12px 16px;
  text-decoration: none;
}

/* Mostrar el contenido del botón desplegable al hacer clic en el botón */
.dropdown:hover .dropdown-content {
  display: block;
}

.drag-container {
  -webkit-box-shadow: 0px 0px 12px 0px rgba(24, 73, 95, 0.3);
  -moz-box-shadow: 0px 0px 12px 0px rgba(24, 73, 95, 0.3);
  box-shadow: 0px 0px 12px 0px rgba(24, 73, 95, 0.3);
  border-radius: 1rem;
  padding: 6rem 2rem;
  margin-top: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 90rem;
}
.drag-container .icon {
  margin-bottom: 4rem;
}

@media (min-width: 768px) {
  .drag-container {
    width: 75%;
  }
}
@media (min-width: 992px) {
  .drag-container {
    width: 55%;
  }
}
@media (min-width: 1600px) {
  .drag-container {
    width: 40%;
  }
}
input::file-selector-button {
  font-family: "Sarabun", sans-serif;
  font-weight: 700;
  background-color: #2be3a6;
  color: #FFFFFF;
  cursor: pointer;
  padding: 1rem;
  border: none;
  border-radius: 3px;
  transition: background-color 0.3s ease;
}
input::file-selector-button:hover {
  background-color: #22bf8a;
}

.drag-text h3 {
  text-align: center;
  color: #2be3a6;
  font-weight: 700;
}

.img-preview {
  width: 100%;
  padding-bottom: 2rem;
}

.bnw-button {
  margin: unset;
  align-self: start;
  cursor: pointer;
  background-color: #434343;
  padding: 0.5rem 1.5rem;
  transition: background-color 0.3s ease;
}
.bnw-button p {
  font-weight: 700;
  font-size: 1.5rem;
  margin: 0;
  color: #d3d3d3;
  transition: color 0.3s ease;
}
.bnw-button:hover {
  background-color: #d3d3d3;
}
.bnw-button:hover p {
  color: #434343;
}

#magnifying-area {
  margin: 0;
  overflow: hidden;
  position: relative;
  width: 100%;
}

.footer {
  padding: 6rem 0 2rem 0;
}

.footer-container h3 {
  text-align: center;
  color: #7d88d5;
  font-weight: 700;
}

.grids {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}

@media (min-width: 480px) {
  .grids {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 768px) {
  .grids {
    grid-template-columns: repeat(5, 1fr);
  }
}
.grid-variation {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.grid-variation p {
  font-size: 1.6rem;
  text-align: center;
  color: transparent;
  font-weight: 700;
  transition: color 0.3s ease;
}
.grid-variation:hover p {
  color: #FFFFFF;
  text-shadow: 1px 1px 4px rgb(24, 73, 95);
}

.grid {
  border-radius: 100%;
  height: 9rem;
  width: 9rem;
}

.grid-container h3 {
  margin-bottom: 2rem;
}

.color1 {
  background-color: var(--color1);
}

.color2 {
  background-color: var(--color2);
}

.color3 {
  background-color: var(--color3);
}

.color4 {
  background-color: var(--color4);
}

.color5 {
  background-color: var(--color5);
}

.color6 {
  background-color: var(--color6);
}

.color7 {
  background-color: var(--color7);
}

.color8 {
  background-color: var(--color8);
}

.color9 {
  background-color: var(--color9);
}

.color10 {
  background-color: var(--color10);
}

.refresh {
  margin-top: 4rem;
  display: flex;
  gap: 0.5rem;
  height: 4.2rem;
  border: none;
  background-color: #2be3a6;
  padding: 1rem;
  border-radius: 0.3rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.refresh:hover {
  background-color: #22bf8a;
}
.refresh p {
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  margin: 0;
}

.dominant {
  padding-top: 4rem;
  justify-content: space-between;
}

@media (min-width: 480px) {
  .dominant {
    display: flex;
  }
}
.dominant-color {
  background-color: var(--dColor);
  height: 13rem;
  width: 13rem;
  border-radius: 100%;
}

.color-animation {
  cursor: pointer;
}
.color-animation:hover {
  animation: pulse 3s ease 0s 1 normal forwards;
}
@keyframes pulse {
  0% {
    animation-timing-function: ease-out;
    transform: scale(1);
    transform-origin: center center;
  }
  10% {
    animation-timing-function: ease-in;
    transform: scale(0.91);
  }
  17% {
    animation-timing-function: ease-out;
    transform: scale(0.98);
  }
  33% {
    animation-timing-function: ease-in;
    transform: scale(0.87);
  }
  45% {
    animation-timing-function: ease-out;
    transform: scale(1);
  }
}

.modal {
  border: none;
  border-radius: 1rem;
  width: 90%;
  max-width: 80rem;
  justify-content: space-between;
  padding: 0;
}
.modal::backdrop {
  background-color: rgba(0, 0, 0, 0.5);
}

@media (min-width: 768px) {
  .modal {
    width: 65%;
  }
}
@media (min-width: 992px) {
  .modal {
    width: 45%;
  }
}
@media (min-width: 1600px) {
  .modal {
    width: 30%;
  }
}
.dsp-flex {
  display: flex;
}

#closeModal {
  padding: 1.1rem;
  border-bottom-left-radius: 2rem;
  border: none;
  background-color: #2be3a6;
  transition: background-color 0.3s ease;
  cursor: pointer;
}
#closeModal span {
  font-weight: 700;
  color: #FFFFFF;
}
#closeModal:hover {
  background-color: #22bf8a;
}

.modal-content {
  overflow-y: auto;
  padding-top: 4rem;
  padding-bottom: 3rem;
  padding-left: 3rem;
  width: 100%;
  scrollbar-width: none; /* Oculta la barra de desplazamiento de Firefox */
  -ms-overflow-style: none; /* Oculta la barra de desplazamiento de IE/Edge */
}
.modal-content::-webkit-scrollbar {
  width: 0; /* Oculta la barra de desplazamiento de WebKit (Safari/Chrome) */
}
.modal-content h3 {
  margin: 0;
  padding-bottom: 2rem;
  text-align: center;
}

.palette {
  display: flex;
  flex-direction: column;
  gap: 0;
}

.palette-tone {
  height: 8.5rem;
}

.ptone {
  background-color: var(--ptone);
}

.tone1 {
  background-color: var(--tone1);
}

.tone2 {
  background-color: var(--tone2);
}

.tone3 {
  background-color: var(--tone3);
}

.tone4 {
  background-color: var(--tone4);
}

.tone5 {
  background-color: var(--tone5);
}

.modal_about {
  border: none;
  border-radius: 1rem;
  width: 90%;
  max-width: 80rem;
  justify-content: space-between;
  padding: 0;
}
.modal_about::backdrop {
  background-color: rgba(0, 0, 0, 0.5);
}

@media (min-width: 768px) {
  .modal_about {
    width: 65%;
  }
}
@media (min-width: 992px) {
  .modal_about {
    width: 45%;
  }
}
@media (min-width: 1600px) {
  .modal_about {
    width: 30%;
  }
}
#closeModalAbout {
  padding: 1.1rem;
  border-bottom-left-radius: 2rem;
  border: none;
  background-color: #2be3a6;
  transition: background-color 0.3s ease;
  cursor: pointer;
}
#closeModalAbout span {
  font-weight: 700;
  color: #FFFFFF;
}
#closeModalAbout:hover {
  background-color: #22bf8a;
}

.link-github {
  font-weight: 700;
}
.link-github span {
  color: #7d88d5;
}
.link-github span:hover {
  color: #6069a9;
}

.light-mode {
  background-color: #f3f3f3;
}
.light-mode .header {
  background-color: #FFFFFF;
}
.light-mode .dropdown-content {
  background-color: #FFFFFF;
  border: 1px solid #d3d3d3;
}
.light-mode .dropdown-content a {
  color: #434343;
}
.light-mode .dropdown-content a:hover {
  background-color: #d3d3d3;
}
.light-mode .drag-container {
  background-color: #FFFFFF;
}
.light-mode .modal {
  background-color: #FFFFFF;
}
.light-mode .modal_about {
  background-color: #FFFFFF;
}
.light-mode .modal-content h3 {
  color: #6069a9;
}
.light-mode .modal-content p {
  color: #434343;
}
.light-mode #fileInput {
  color: #434343;
}
.light-mode .grid-container h3 {
  color: #6069a9;
}

.dark-mode {
  background-color: #434343;
}
.dark-mode .header {
  background-color: #232323;
}
.dark-mode .dropdown-content {
  background-color: #434343;
  border: 1px solid #7d88d5;
}
.dark-mode .dropdown-content a {
  color: #FFFFFF;
}
.dark-mode .dropdown-content a:hover {
  background-color: #232323;
}
.dark-mode .drag-container {
  background-color: #232323;
}
.dark-mode .modal {
  background-color: #434343;
}
.dark-mode .modal_about {
  background-color: #434343;
}
.dark-mode .modal-content h3 {
  color: #7d88d5;
}
.dark-mode .modal-content p {
  color: #FFFFFF;
}
.dark-mode #fileInput {
  color: #FFFFFF;
}
.dark-mode .grid-container h3 {
  color: #7d88d5;
}